import React, { useState, useEffect,  useRef } from 'react';
import { MdRefresh, MdOutlineRemoveShoppingCart, MdModeEditOutline, MdCheck } from 'react-icons/md'
import CreateOrder from './add/createOrder';
import axios from 'axios'
import { FaUserEdit, FaCheckCircle } from 'react-icons/fa'
import {GiMoneyStack} from 'react-icons/gi'
import { MdOutlineError } from 'react-icons/md'
import moment from 'moment';
import { Link, useParams } from 'react-router-dom';
import loadingimg from '../images/loading.jpg'
import errimg from '../images/err.jpg'
import successimg from '../images/success.jpg'
import zero from '../images/zero.jpg'
import {BiDetail} from 'react-icons/bi'
import ReactToPrint from 'react-to-print';

import { jsPDF } from "jspdf";
import html2canvas from 'html2canvas';
import { IoMdPersonAdd, IoMdSearch } from 'react-icons/io'

function CustomerOrders(props) {
  const {flock} = useParams()
  const [search, setsearch] = useState()
  const [editData, setEditData] = useState()
  const [Refresh, setRefresh] = useState(0)
  const componentRef = useRef();

  const [data, setdata] = useState(props.data)
  const [row, setrow] = useState(props.data)
  const [err, setErr] = useState(false)

  //edit form

  const [Total, setTotal] = useState(0)
  const [customer, setcustomer] = useState(props.customer)


  const [received_payment, setreceived_payment] = useState()
  const [Net_Amount, setNet_Amount] = useState('')
  const [Balance, setBalance] = useState("")
  const [Net_Balance, setNet_Balance] = useState("")
  const [Net_received, setNet_received] = useState(0)


  const [editsuccess, seteditsuccess] = useState(false)
  const [editerr, setediterr] = useState(false)

  const [deleteerr, setdeleteerr] = useState(false)
  const [deletesuccess, setdeletesuccess] = useState(false)
  useEffect(() => {

    // axios.get('http://localhost:5000/orders')
    //   .then(function (response) {
    //     // handle success'
        setdata(props.data)
        setrow(props.data)
        setcustomer(props.customer)
        console.log(customer)
    //     console.log(response.data);
    //   })
    //   .catch(function (error) {
    //     // handle error
    //     setErr(true)
    //     console.log(error);
    //   })
  }, [props]);
  const handleEdit = (id) => {
    const filteredit = data.filter(data => data.id == id)
    setEditData(filteredit[0])
    setTotal(filteredit[0].Total_payment)
    setBalance(filteredit[0].Net_Balance)
    setNet_Balance(filteredit[0].Net_Balance)
    setNet_received(filteredit[0].Net_received)
    setreceived_payment(filteredit[0].Net_received)

  };



  const [success, setsuccess] = useState(false)

  const handleEditFrom = (e) => {
    e.preventDefault()
    const data = {
      Net_received: Net_received,
      Net_Balance:Net_Balance,
      
      date: moment().format("MMM Do YY"),
      time: moment().format(" h:mm:ss a")
    }
    axios.put(`http://localhost:5000/order/${editData.id}`, data, {
      headers: {'Content-Type': 'application/json',}
    } 
     

    )
      .then(function (response) {
        console.log(response);
        console.log(response.data)

        if (response.data.edit) {
       
  props.RefreshData()

  setreceived_payment('')



          seteditsuccess(true)
        } else {
          setediterr(true)
        }

        RefreshData()

      })
      .catch(function (error) {
        console.log(error);
        setediterr(true)
      });
  }
  const closeModel = () => {
    seteditsuccess(false)
    setediterr(false)
    setsuccess(false)

  }



  const RefreshData = () => {
    setRefresh(Refresh + 1)
    console.log("data refresh: " + Refresh)
  };


  const deleteOrder = (id) => {
    console.log("delete id: " + id)
    axios.delete(`http://localhost:5000/order/${id}`)
      .then(function (response) {
        // handle success'
        if (response.data.delete) {
          setRefresh(Refresh + 1)
          setdeleteerr(false)
          setdeletesuccess(true)
          setTimeout(() => setdeletesuccess(false), 5000);

        } else {
          setdeleteerr(true)
        }
        console.log(response.data.msg);
        setRefresh(Refresh + 1)

      })
      .catch(function (error) {
        // handle error
        setdeleteerr(true)
        console.log(error);
      })
  };
  useEffect(() => {
    searchResult()
  }, [search]);
  const searchResult = async (e) => {
    if (search) {

      const results = data.filter(data =>
        data.id == search || data.driver.toLowerCase().includes(search) || data.broker.toLowerCase().includes(search)
      );
      console.log("result :", results)
      setrow(results);
    } else {
      setrow(data);
      console.log("row :", row)

    }

  }
  const handlesearch = event => {
    if (event.target.value) {
      setsearch(event.target.value);

    } else {
      setrow(data);
    }
  };


  const printDocument = () =>{
    const input = document.getElementById('PrintCOrder');
    html2canvas(input)
      .then((canvas) => {
        const imgData = canvas.toDataURL('image/jpg');
       
        const pdf = new jsPDF({
          orientation: "portrait",
          unit: "in",
          format: [5, 8]
        });
        pdf.addImage(imgData, 'JPEG', 0, 0);
        pdf.save();
    //     console.log(imgData)
    //     var myWindow = window.open("", "Image");
    // myWindow.document.write("<img src='"+imgData+"''>");
    // myWindow.print();
      })
    ;
  }


  useEffect(() => {
    // var received = Number(received_payment) + advance

    if(Balance < 0){
      let net_pay = Number(received_payment)  -  Number(Net_Amount)
      setNet_Balance(Balance + Number(Net_Amount))
      setNet_received(net_pay)
      console.log("received: " + Net_received)
    }else{
      let net_pay = (Number(received_payment) + Number(Net_Amount))
      setNet_received(net_pay)
      setNet_Balance(Balance - Number(Net_Amount))

      console.log(net_pay)
    }
  }, [ Net_Amount]);


  
  const [cflock, setcflock] = useState()

  useEffect(() => {
      axios.get(`http://localhost:5000/flock/${flock}`)
          .then(function (response) {
              // handle success'
              console.log(response.data.flock)
              setcflock(response.data.flock.flock)
  
          })
          .catch(function (error) {
              // handle error
              console.log(error);
          })
  }, [flock]);
  return (<>

    <div className="card" >
      <div className="card-header bg-success ">
        <div className="row">
          <div className="col-md-5">
            <p className=" m-0 text-light pt-2">Orders</p>
          </div>
          <div className="col-md-7">
          <div className="searchInput">
                <span className="icon"><IoMdSearch /></span>
                <input type="search" id="search" onChange={handlesearch} placeholder="Search..." />
              </div>
          <button className="btn-icon   float-end shadow" style={{marginRight: ".5em"}} data-toggle="popover" aria-label="refresh"
                      onClick={()=> props.RefreshData()}
                      data-cooltipz-dir="bottom"><MdRefresh /></button>
          </div>
        </div>

      </div>
      <div className="card-body ">
        {deletesuccess && <div class="alert alert-success d-flex align-items-center" role="alert">
          <FaCheckCircle />
          <div>
            Order has been deleted successfully
          </div>
        </div>}
        {deleteerr && <div class="alert alert-danger d-flex align-items-center" role="alert">
          <MdOutlineError />
          <div>
            Order could not delete. Please try again or Restart your pc || app
          </div>
        </div>}
        {!err ? data && row ? !data.length == 0? <><div className="table-responsive">
          <table className="table table-bordered table-striped table-hover" >
            <caption>Orders</caption>
            <thead className="">
              <tr>
                <th >#</th>
                <th >order#</th>

                <th >Car</th>
                <th >Broker</th>
                <th >Weight</th>
                <th >Payments</th>

                <th >Date</th>
                <th >Actions</th>


              </tr>
            </thead>
            <tbody>
              {row.map((data, index) =>
                <tr key={index} ><td scope="row">{index + 1}</td>
                  <td>{cflock}
                  </td>
                  <td>Driver: {data.driver}<br />
                    <small>Car No# {data.Car_no}</small>
                  </td>
                  <td>{data.broker}
                  </td>
                  <td>First weight: {data.f_weight}<br />
                    Second weight:  {data.l_weight}<br />Net weight: {data.Net_weight}
                  </td>
                  <td>Rate: {data.Rate}, Total: {data.Total_payment}<br />
                    Advancev:  {data.advance}, received (2nd payment):  {data.received_payment}<br />
                    Net Received :  {data.Net_received}<br />
                     <span className="text-danger">Balance: {data.Balance}</span>, Net Balance: {data.Net_Balance}
                  </td>
                  <td>{data.Date}<br />
                    <small>{data.time}</small>
                  </td>
                  <td >

                    <button className="btn-icon bg-danger  float-end shadow action mt-2  " data-toggle="popover" aria-label="delete order"
                      onClick={() => deleteOrder(data.id)}
                      data-cooltipz-dir="bottom"><MdOutlineRemoveShoppingCart /></button>

                    <button type="button"
                      className="btn-icon bg-warning float-end shadow action mt-2 " data-toggle="popover"
                      data-bs-toggle="modal"
                      data-bs-target="#EditOrder"
                      onClick={() => handleEdit(data.id)}
                      aria-label="Edit this Order"
                      data-cooltipz-dir="bottom"><GiMoneyStack /></button>
                     
                       
                      <button type="button"
                      className="btn-icon bg-warning float-end shadow action mt-2 " data-toggle="popover"
                      data-bs-toggle="modal"
                      data-bs-target="#corderdetail"
                      onClick={() => handleEdit(data.id)}
                      aria-label="Order detail"
                      data-cooltipz-dir="bottom"><BiDetail /></button>
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
        </>: <>
          <img src={zero} width="600px" height="400px" />
          <h2 className="text-info">Sorry There are no Orders of this customer</h2>
        </>  : <>
          <img src={loadingimg} width="600px" height="400px" />
          <h2 className="text-info">Loading... </h2>
        </>
          : <>
            <img src={errimg} width="600px" height="400px" />
            <h3 className="text-danger">Someting is wrong please Restart your app </h3>
          </>}
      </div>






      <div className="modal fade" id="EditOrder" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">Edit Order</h5>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div className="modal-body">
            <div className="row-one" >

<p className="p-title" >Order#</p>
<p className="p-value " style={{ width: "10em" }} >{cflock}</p>
<p className="p-title"  >Date:</p>
<p className="p-value" style={{ width: "16em" }}>{editData && editData.Date} {editData && editData.time}</p>
</div>
              {!editerr ? !editsuccess ? <>
                {data ? <form onSubmit={(e) => handleEditFrom(e)}>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Customer</label>
                    <input type="Text"
                      className="form-control shadow-none"
                      disabled
                      value={customer.Name}
                    />
                  </div>
                    
                  
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Net Received</label>
                    <input type="number" 
                     className={`form-control shadow-none`}

                      placeholder="Enter Advance payment"
                      value={Net_received}
                      disabled

                    />
                  </div>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Total</label>
                    <input type="number" step="0.01"
                      className="form-control shadow-none"
                      value={Total}
                      disabled
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Net Balance</label>
                    <input type="number" 
                     className={`form-control shadow-none`}

                      placeholder="Enter Advance payment"
                      value={Net_Balance}
                      disabled
                    />
                  </div>
                  {Balance <= 0 ?<div className="input-group mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="basic-addon1">-</span>
                    </div>
                    <input type="number" 
                    className={`form-control shadow-none `}
                    placeholder={Net_Amount}
                      value={Math.abs(Net_Amount)}
                      onChange={(e) => setNet_Amount(e.target.value)}
                      required
                    />
                  </div>
                  :
                  <div className="input-group mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="basic-addon1">+</span>
                    </div>
                    <input type="number" 
                    className={`form-control shadow-none `}
                    placeholder={Net_Amount}
                      value={Net_Amount}
                      onChange={(e) => setNet_Amount(e.target.value)}
                      required
                    />
                  </div>}

                  <div className="modal-footer">
                    <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                    <button type="submit" className="btn btn-primary">Save</button>
                  </div>
                </form> : <h1>Loading..</h1>}
              </> : <>
                <img src={successimg} width="400px" height="400px" />
                <h2 className="text-success">Customer added Succesfully</h2>
                <div className="modal-footer">
                  <button type="button" className="btn btn-secondary" onClick={closeModel} data-bs-dismiss="modal">Close</button>
                </div>
              </>
                :
                <>
                  <img src={errimg} width="400px" height="400px" />
                  <h2 className="text-danger">Sorry something is wrong, Please Try Again</h2>
                  <div className="modal-footer">
                    <button type="button" className="btn btn-secondary" onClick={closeModel} data-bs-dismiss="modal">Close</button>
                  </div>
                </>
              }
            </div>
          </div>
        </div>
        {/* {editData && <EditCustomer Refresh={RefreshData}  data={editData} />} */}

      </div>
 



      <div className="modal fade" id="corderdetail" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">Order Details</h5>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div className="modal-body" ref={componentRef} id="PrintCOrder">
              < >
              
              <dl class="row"  >
                  <div className="col-md-12   text-center">
                    <h2 className=" text-primary">Sanan Poultry Farm </h2>
                    <p><span className="bg-primary text-light p-1">Zarobai, Topi, Swabi, Khyber Pakhtunkhwa</span><br />
                      Phone: 0345-8585889</p>


                  </div>
                  <div className="row-one" >

                    <p className="p-title" >Order#</p>
                    <p className="p-value " style={{ width: "10em" }} >{flock}</p>
                    <p className="p-title"  >Date:</p>
                    <p className="p-value" style={{ width: "16em" }}>{editData && editData.Date} {editData && editData.time}</p>
                  </div>
                  <hr />
                  <div className="row-one" >
                    <p className="p-title" >Customer:</p>
                    <p className="p-value" >{customer && customer.Name}</p>
                    <p className="p-title" >Broker:</p>
                    <p className="p-value" >{editData && editData.broker}</p>
                  </div>
                  <hr />

                  <div className="p-heading" >Car</div>

                  <div className="row-one" >

                    <p className="p-title" >Driver:</p>
                    <p className="p-value" >{editData && editData.driver}</p>
                    <p className="p-title" >Car#</p>
                    <p className="p-value">{editData && editData.Car_no}</p>
                  </div>
                  <hr />
                  <div className="p-heading">Weight</div>
                  <>
                    <div className="row-one">
                      <div className="p-title" style={{ width: "15em" }}>Car weight (first weight)</div>
                      <div className="p-value">{editData && editData.f_weight}</div>
                    </div>
                    <div className="row-one">
                      <div className="p-title" style={{ width: "15em" }}>Car weight (Second weight)</div>
                      <div className="p-value">{editData && editData.l_weight}</div>
                    </div>
                    <p className="row-one pb-2">
                      <div className="p-title" style={{ width: "15em" }}>Net Weight</div>
                      <div className="p-value">{editData && editData.Net_weight}</div>
                    </p>
                  </>

                  <hr />
                  <div className="p-heading mt-0 mb-1 text-center">Payments</div>
                  <>
                    <div className="row-one">
                      <div className="p-title">Rate</div>
                      <div className="p-value"> {editData && editData.Rate}</div>
                      <div className="p-title" style={{width: "16em"}}>Advance (1st payment)</div>
                      <div className="p-value" style={{width: "5em"}}>{editData && editData.advance}</div>

                    </div>
                    <div className="row-one ">
                    <div className="p-title"></div>
                      <div className="p-value"> </div>

                      <div className="p-title" style={{width: "16em"}}>Received (2nd payment)</div>
                      <div className="p-value" style={{width: "5em"}}>{editData && editData.Net_received}</div>
                  
                    </div>
                    <div className="row-one  ">
                    <div className="p-title">Total</div>
                      <div className="p-value"> {editData && editData.Total_payment}</div>
                    <div className="p-title" style={{width: "16em"}}>Net Balance</div>
                      <div className="p-value" style={{width: "5em"}}>{editData && editData.Net_Balance}</div>
                   </div>
                    
                    
                  </>

                  <hr />

                  <div className="row-one" style={{ justifyContent: "space-between" }}>
                    <div className="sign"  >Farm</div>
                    <div className="sign">Receiver</div>
                  </div>


                </dl>

              </>

            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
              <ReactToPrint
              copyStyles="true"
                trigger={() => <button type="button" className="btn btn-warning" data-bs-dismiss="modal">Print</button>
                }
                content={() => componentRef.current}
              />
             <button type="button" onClick={printDocument} className="btn btn-primary">Download</button>
            </div>
          </div>
        </div>
      </div>




      


    </div>
  </>
  );
}

export default CustomerOrders

