
import { Home } from "./Pages/Home";
import { Flock } from "./Pages/flock";

import {
  BrowserRouter as Router,
  Routes,
  Route
} from "react-router-dom";
import { Customer } from "./Pages/customer";
function App() {
  return (
    <div className="App">
      <Router>
        <Routes>
          <Route path="/" element={<Home/> } >
          <Route path="home/:id" element={<Flock/> } />

          </Route>
          

        <Route exact  path="/customer/:cid/:flock" element={<Customer/>}/>
        </Routes>
      </Router>
    </div>
  );
}

export default App;
