import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { MdRefresh } from 'react-icons/md'
import Plack from './plack';
import { Link, useParams } from 'react-router-dom';

function Sparkboxes(props) {
  const [data, setData] = useState()
  const [Refresh, setRefresh] = useState(0)
  const {id} = useParams()
  useEffect(() => {
    axios.get(`http://localhost:5000/states/${id}`)
      .then(function (response) {
        // handle success'
        setData(response.data)
        console.log(response.data);
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
  }, [Refresh, id]);
  return (<>
    <div className="row sparkboxes">
      
      <div className="col-md-2">
        <div className="box box1 ">
          <h6 style={{ margin: 0 }}>Sold</h6>
          <h2 className="spark-box-value">{data && data.sold} kg</h2>
          <button className="btn-icon   float-end shadow" style={{ marginRight: ".5em" }} data-toggle="popover" aria-label="refresh states"
                                onClick={() => setRefresh(Refresh + 1)}
                                data-cooltipz-dir="bottom"><MdRefresh /></button>
        </div>
      </div>
      <div className="col-md-2">
        <div className="box box2 ">
          <h6 style={{ margin: 0 }}>Total Earn</h6>
          <h2 className="spark-box-value">Rs. {data && data.earn} </h2>
        </div>
      </div>
      <div className="col-md-2">
        <div className="box box3 ">
          <h6 style={{ margin: 0 }}>Received Payment</h6>
          <h2 className="spark-box-value">Rs. {data && data.received_payment} </h2>
        </div>
      </div>
      
      <div className="col-md-2">
        <div className=" box box4 " >
          <h6 style={{ margin: 0 }}>Panding Payments</h6>
          <h2 className="spark-box-value">Rs. {data && data.pandingpayment} </h2>
        </div>
      </div>
      <div className="col-md-2">
        <div className="box box3 ">
          <h6 style={{ margin: 0 }}>Orders #</h6>
          <h2 className="spark-box-value">{data && data.x} </h2>
        </div>
      </div>
      <div className="col-md-2">
        <div className="box box4 ">
          <h6 style={{ margin: 0 }}>Customers #</h6>
          <h2 className="spark-box-value">{data && data.customers} </h2>

        </div>
      </div>

    </div>

  </>
  );
}

export default Sparkboxes
