import React, { useState, useEffect } from 'react';
import { IoMdPersonAdd, IoMdSearch } from 'react-icons/io'
import { FaUserEdit, FaCheckCircle } from 'react-icons/fa'
import { AiOutlineUserDelete } from 'react-icons/ai'
import { MdOutlineError } from 'react-icons/md'
import AddCustomer from './add/addCustomer';
import axios from 'axios'
import { Link } from 'react-router-dom';
import loadingimg from '../images/loading.jpg'
import errimg from '../images/err.jpg'
import successimg from '../images/success.jpg'
import zero from '../images/zero.jpg'

 
import {useParams} from "react-router-dom"
function Customers() {
  const {id} = useParams()
  const [search, setsearch] = useState()

  const [data, setdata] = useState()
  const [row, setrow] = useState([])
  const [Refresh, setRefresh] = useState(0)
  const [err, setErr] = useState(false)
  const [deleteerr, setdeleteerr] = useState(false)
  const [deletesuccess, setdeletesuccess] = useState(false)
  //edit form
  const [name, setName] = useState("")
  const [phone, setPhone] = useState("")
  const [phone1, setPhone1] = useState("")
  const [address, setAddress] = useState("")
  const [des, setDes] = useState("")
  const [editsuccess, seteditsuccess] = useState(false)
  const [editerr, setediterr] = useState(false)

  useEffect(() => {
    searchResult()
  }, [search]);

  useEffect(() => {
    axios.get('http://localhost:5000/customers')
      .then(function (response) {
        // handle success'
        setdata(response.data.customers)
        setrow(response.data.customers)
        console.log(response.data.customers);
      })
      .catch(function (error) {
        // handle error
        setErr(true)
        console.log(error);
      })
  }, [Refresh]);
  const searchResult = async (e) => {
    if (search) {

      const results = data.filter(data =>
        data.Name.toLowerCase().includes(search)
      );
      console.log("result :", results)
      setrow(results);
    } else {
      setrow(data);
      console.log("row :", row)

    }

  }
  const handlesearch = event => {
    if (event.target.value) {
      setsearch(event.target.value);

    } else {
      setrow(data);
    }
  };
  const [editData, setEditData] = useState()
  const handleEdit = (id) => {
    const filteredit = data.filter(data => data.id == id)
    setEditData(filteredit[0])
    setName(filteredit[0].Name)
    setPhone(filteredit[0].phone)
    setPhone1(filteredit[0].phone1)
    setAddress(filteredit[0].address)
    setDes(filteredit[0].des)
    console.log(filteredit)
  };

  const handleEditFrom = () => {
    

    axios.put(`http://localhost:5000/customer/${editData.id}`, {
    Name: name,
    phone: phone,
    phone1: phone1,
    address: address,
    des: des}
     )
      .then(function (response) {
        console.log(response);
        if (response.data.update) {
          setName('')
          setPhone('')
          setPhone1('')
          setAddress('')
          setDes('')
          seteditsuccess(true)
        } else {
          setediterr(true)
        }

        RefreshData()

      })
      .catch(function (error) {
        console.log(error);
        setediterr(true)
      });
  }
  const closeModel = () => {
    seteditsuccess(false)
    setediterr(false)
  }

  const RefreshData = () => {
    setRefresh(Refresh + 1)
    console.log("data refresh: " + Refresh)
  };

  const deleteCustomer = (id) => {
    console.log("delete id: " + id)
    axios.delete(`http://localhost:5000/customer/${id}`)
      .then(function (response) {
        // handle success'
        if (response.data.delete) {
          setRefresh(Refresh + 1)
          setdeleteerr(false)
          setdeletesuccess(true)
          setTimeout(() => setdeletesuccess(false), 5000);

        } else {
          setdeleteerr(true)
        }
        console.log(response.data.msg);
        setRefresh(Refresh + 1)

      })
      .catch(function (error) {
        // handle error
        setdeleteerr(true)
        console.log(error);
      })
  };

  return (<>

    <div className="card" >
      <div className="card-header bg-success">
        <div className="row">
          <div className="col-md-5">
            <p className="m-0 pt-2 card-title text-light">Customers</p>
          </div>
          <div className="col-md-7">
            <div className="">
              <div className="searchInput">
                <span className="icon"><IoMdSearch /></span>
                <input type="search" id="search" onChange={handlesearch} placeholder="Search..." />
              </div>
            </div>
            <AddCustomer Refresh={RefreshData} />
          </div>
        </div>
      </div>
      <div className="card-body ">
        {deletesuccess && <div class="alert alert-success d-flex align-items-center" role="alert">
          <FaCheckCircle />
          <div>
            Customer has been deleted successfully
          </div>
        </div>}
        {deleteerr && <div class="alert alert-danger d-flex align-items-center" role="alert">
          <MdOutlineError />
          <div>
            User could not delete. Please try again or Restart your pc || app
          </div>
        </div>}
        {!err ? data && row ? !data.length == 0? <><div className="table-responsive">
          <table className="table table-bordered table-striped table-hover" >
            <caption>All Your Customers</caption>
            <thead className="">
              <tr>
                <th >#</th>
                <th >Name</th>
                <th scope="col">contacts</th>
                <th scope="col">Actions</th>
              </tr>
            </thead>
            <tbody>
              {row.map((data, index) =>
                <tr key={index}><td scope="row">{index + 1}</td>
                  <td><Link to={`/customer/${data.id}/${id}`}>
                    {data.Name} <br />
                    <small>from: {data.date}</small>
</Link>
                  </td>
                  <td>{data.phone}<br />
                    {data.phone1}
                  </td>
                  <td >
                    <button className="btn-icon bg-danger action bg-info mt-2 float-end shadow" data-toggle="popover" onClick={() => deleteCustomer(data.id)} aria-label="Delete Customer" data-cooltipz-dir="bottom"><AiOutlineUserDelete /></button>

                    <button className="btn-icon bg-warning action bg-info mt-2 float-end shadow" data-toggle="popover" aria-label="Edit Customer" data-cooltipz-dir="bottom"
                      data-bs-toggle="modal"
                      data-bs-target="#edit"
                      onClick={() => handleEdit(data.id)}
                    ><FaUserEdit /></button>
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div></> : <>
          <img src={zero} width="600px" height="400px" />
          <h2 className="text-info">Sorry There are No customers </h2>
        </> : <>
          <img src={loadingimg} width="600px" height="400px" />
          <h2 className="text-info">Loading... </h2>
        </>
          : <>
            <img src={errimg} width="600px" height="400px" />
            <h3 className="text-danger">Someting is wrong please Restart your app </h3>
          </>
        }


      </div>


      <div className="modal fade" id="edit" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">Edit Customer</h5>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div className="modal-body">
              {!editerr ? !editsuccess ? <>
                {data ? <>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Name</label>
                    <input type="Text"
                      className="form-control shadow-none"
                      placeholder="Enter Customer Name"
                      value={name}
                      onChange={(e) => setName(e.target.value)}
                      required
                    />
                  </div>
                  <div className="row">
                    <div className="col-md-6 mb-3">
                      <label for="exampleInputEmail1" className="form-label">Phone 1</label>
                      <input type="Text"
                        className="form-control shadow-none"
                        placeholder="Enter phone number"
                        value={phone}
                        onChange={(e) => setPhone(e.target.value)}
                        required
                      />
                    </div>
                    <div className="col-md-6 mb-3">
                      <label for="exampleInputEmail1" className="form-label">Phone 2</label>
                      <input type="Text"
                        placeholder="Enter another phone number"
                        className="form-control shadow-none"
                        value={phone1}
                        onChange={(e) => setPhone1(e.target.value)}
                      />
                    </div>
                  </div>
                  <div className=" mb-3">
                    <label for="exampleInputEmail1" className="form-label">Address</label>
                    <input type="Text"
                      placeholder="Enter Customer Address"
                      className="form-control shadow-none"
                      value={address}
                      onChange={(e) => setAddress(e.target.value)}
                    />
                  </div>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Description</label>
                    <textarea type="Text"
                      className="form-control shadow-none"
                      placeholder="Enter Customer Description "
                      value={des}
                      onChange={(e) => setDes(e.target.value)}
                    />
                  </div>
                  <div className="modal-footer">
                    <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                    <button type="submit" onClick={handleEditFrom} className="btn btn-primary">Save</button>
                  </div>
                </> : <h1>Loading..</h1>}
              </> : <>
                <img src={successimg} width="400px" height="400px" />
                <h2 className="text-success">Customer added Succesfully</h2>
                <div className="modal-footer">
                  <button type="button" className="btn btn-secondary" onClick={closeModel} data-bs-dismiss="modal">Close</button>
                </div>
              </>
                :
                <>
                  <img src={errimg} width="400px" height="400px" />
                  <h2 className="text-danger">Sorry something is wrong, Please Try Again</h2>
                  <div className="modal-footer">
                    <button type="button" className="btn btn-secondary" onClick={closeModel} data-bs-dismiss="modal">Close</button>
                  </div>
                </>
              }
            </div>
          </div>
        </div>
        {/* {editData && <EditCustomer Refresh={RefreshData}  data={editData} />} */}

      </div>


    </div>
  </>
  );
}

export default Customers