import React, { useState, useEffect, useRef } from 'react';
import { MdRefresh, MdOutlineRemoveShoppingCart, MdModeEditOutline, MdCheck } from 'react-icons/md'
import Payorder from './add/pay';
import axios from 'axios'
import { FaUserEdit, FaCheckCircle } from 'react-icons/fa'
import { MdOutlineError } from 'react-icons/md'
import moment from 'moment';
import { Link } from 'react-router-dom';
import loadingimg from '../images/loading.jpg'
import errimg from '../images/err.jpg'
import successimg from '../images/success.jpg'
import { jsPDF } from "jspdf";
import html2canvas from 'html2canvas';
import {BiDetail} from 'react-icons/bi'
function CustomerPayments(props) {
  const [search, setsearch] = useState()
  const [editData, setEditData] = useState()
  const [Refresh, setRefresh] = useState(0)

  const [data, setdata] = useState()
  const [row, setrow] = useState(data)
  const [Customer, setCustomer] = useState(props.customer)

  const [err, setErr] = useState(false)

  const [deleteerr, setdeleteerr] = useState(false)
  const [deletesuccess, setdeletesuccess] = useState(false)
  useEffect(() => {
    if(props.id){
    axios.get(`http://localhost:5000/payments/${props.id}`)
      .then(function (response) {
        // handle success'
        setdata(response.data)
        setrow(response.data)
        console.log(response.data);
        setErr(false)
        setCustomer(props.customer)

      })
      .catch(function (error) {
        // handle error
        setErr(true)
        console.log(error);
      })}
  }, [Refresh, props]);
  const handleEdit = (id) => {
    const filteredit = data.filter(data => data.id == id)
    setEditData(filteredit[0])
  };





  const RefreshData = () => {
    setRefresh(Refresh + 1)
    console.log("data refresh: " + Refresh)
  };


  const deleteOrder = (id) => {
    console.log("delete id: " + id)
    axios.delete(`http://localhost:5000/payment/${id}`)
      .then(function (response) {
        // handle success'
        if (response.data.delete) {
          setRefresh(Refresh + 1)
          setdeleteerr(false)
          setdeletesuccess(true)
          setTimeout(() => setdeletesuccess(false), 5000);

        } else {
          setdeleteerr(true)
        }
        console.log(response.data.msg);
        setRefresh(Refresh + 1)

      })
      .catch(function (error) {
        // handle error
        setdeleteerr(true)
        console.log(error);
      })
  };

  const printDocument = () =>{
    const input = document.getElementById('payments');
    html2canvas(input)
      .then((canvas) => {
        const imgData = canvas.toDataURL('image/jpg');
       
        const pdf = new jsPDF({
          orientation: "portrait",
          unit: "in",
          format: [5, 8]
        });
        pdf.addImage(imgData, 'JPEG', 0, 0);
        pdf.save();
    //     console.log(imgData)
    //     var myWindow = window.open("", "Image");
    // myWindow.document.write("<img src='"+imgData+"''>");
    // myWindow.print();
      })
    ;
  }


  return (<>

    <div className="card" >
      <div className="card-header bg-success ">
        <div className="row">
          <div className="col-md-5">
            <p className=" m-0 text-light pt-2">Payments</p>
          </div>
          <div className="col-md-7">
            <button className="btn-icon   float-end shadow" style={{marginRight: ".5em"}} data-toggle="popover" aria-label="refresh"
                      onClick={() => setRefresh(Refresh + 1)}
                      data-cooltipz-dir="bottom"><MdRefresh /></button>
          </div>
        </div>

      </div>
      <div className="card-body ">
        {deletesuccess && <div class="alert alert-success d-flex align-items-center" role="alert">
          <FaCheckCircle />
          <div>
            Payment has been deleted successfully
          </div>
        </div>}
        {deleteerr && <div class="alert alert-danger d-flex align-items-center" role="alert">
          <MdOutlineError />
          <div>
          Payment could not delete. Please try again or Restart your pc || app
          </div>
        </div>}
        {!err ? data && row ? <><div className="table-responsive">
          <table className="table table-bordered table-striped table-hover" >
            <caption>Customer Payments</caption>
            <thead className="">
              <tr>
                <th >#</th>
                <th>order#</th>
                <th >Pay</th>

                <th >Date</th>
                <th >Actions</th>


              </tr>
            </thead>
            <tbody>
              {row.map((data, index) =>
                <tr key={index} ><td scope="row">{index + 1}</td>
                
                  <td>{data.order.id<= 9 ? "000" + data.order.id: data.order.id<=99? "00"+data.order.id: data.order.id<=999? "0"+data.order.id: data.order.id}
                  </td>
                  
                 
                
                  <td>{data.pay}
                  </td>
                  
                  <td>{data.date}<br />
                    <small>{data.time}</small>
                  </td>
                  <td >

                    <button className="btn-icon bg-danger  float-end shadow action mt-2  " data-toggle="popover" aria-label="delete payment"
                      onClick={() => deleteOrder(data.id)}
                      data-cooltipz-dir="bottom"><MdOutlineRemoveShoppingCart /></button>

                    <button type="button"
                      className="btn-icon bg-warning float-end shadow action mt-2 " data-toggle="popover"
                      data-bs-toggle="modal"
                      data-bs-target="#paydetail"
                      onClick={() => handleEdit(data.id)}
                      aria-label="Payment detail"
                      data-cooltipz-dir="bottom"><BiDetail /></button>
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
        </> : <>
          <img src={loadingimg} width="600px" height="400px" />
          <h2 className="text-info">Loading... </h2>
        </>
          : <>
            <img src={errimg} width="600px" height="400px" />
            <h3 className="text-danger">Someting is wrong please Restart your app </h3>
          </>}
      </div>




      <div className="modal fade" id="paydetail" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">Payment Details</h5>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div className="modal-body" id="payments">
              < >
              
            <dl class="row"  >

            <div className="col-md-12  pb-4 text-center">
              <h2 className=" text-primary">Sanan Poultry Farm </h2>
              <p><span className="bg-primary text-light p-1">Zarobai, Topi, Swabi, Khyber Pakhtunkhwa</span><br />
              Phone: 0345-8585889</p>


              </div>
              <div className="row">

                <dt class="col-sm-3">Order#</dt>
                <dd class="col-sm-3">{editData && <>{editData.order.id<= 9 ? "000" + editData.order.id: editData.order.id<=99? "00"+editData.order.id: editData.order.id<=999? "0"+editData.order.id: editData.order.id}</>}</dd>

                <dt class="col-sm-3">Payment#</dt>
                <dd class="col-sm-3 ">
                {editData && <>{editData.id<= 9 ? "000" + editData.id: editData.id<=99? "00"+editData.id: editData.id<=999? "0"+editData.id: editData.id}</>}
                </dd>
                </div>
                <hr/>
                <dt class="col-sm-3">Customer</dt>
                <dd class="col-sm-6">{Customer && Customer.Name}</dd>
                <hr/>
                
                <dt class="col-sm-3">Payments</dt>
                    <dd class="col-sm-9">
                      <dl class="row">
                      <dt class="col-sm-4">Pay Now</dt>
                        <dd class="col-sm-8">{editData && editData.pay}</dd>
                      <dt class="col-sm-4">Paid</dt>
                        <dd class="col-sm-8">{editData && editData.order.paid}</dd>
                      <dt class="col-sm-4">Pending</dt>
                        <dd class="col-sm-8">{editData && editData.order.panding}</dd>
                        <dt class="col-sm-4">Total</dt>
                        <dd class="col-sm-8">{editData && editData.order.total}</dd>
                      </dl>
                </dd>
                <hr/>


                <dd class="col-sm-6 pt-4">____________________</dd>
                <dd class="col-sm-6 pt-4">____________________</dd>
                <dt class="col-sm-6 text-truncate">Farm</dt>
                <dt class="col-sm-6 text-truncate">Receiver</dt>

                

              </dl>

              </>

            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
             <button type="button" onClick={printDocument} className="btn btn-primary">Print</button>
            </div>
          </div>
        </div>
      </div>


    </div>
  </>
  );
}

export default CustomerPayments


