import React, { useState, useEffect, useRef } from 'react';
import { MdOutlineAddShoppingCart, MdOutlineRemoveShoppingCart, MdModeEditOutline, MdCheck } from 'react-icons/md'
import CreateOrder from './add/createOrder';
import axios from 'axios'
import { FaUserEdit, FaCheckCircle } from 'react-icons/fa'
import { MdOutlineError } from 'react-icons/md'
import moment from 'moment';
import { BiDetail } from 'react-icons/bi'
import { jsPDF } from "jspdf";
import html2canvas from 'html2canvas';
import loadingimg from '../images/loading.jpg'
import errimg from '../images/err.jpg'
import successimg from '../images/success.jpg'
import zero from '../images/zero.jpg'
import { IoMdSearch } from 'react-icons/io'
import { Link, useParams, } from 'react-router-dom';

import ReactToPrint from 'react-to-print';


function PendingOrders(props) {
  const { id } = useParams()
  const componentRef = useRef();
  const [search, setsearch] = useState()
  const [editData, setEditData] = useState()
  const [Refresh, setRefresh] = useState(0)

  const [data, setdata] = useState()
  const [row, setrow] = useState(data)
  const [err, setErr] = useState(false)

  //edit form
  const [car, setcar] = useState("")
  const [broker, setbroker] = useState("")
  const [F_weight, setF_weight] = useState("")
  const [S_weight, setS_weight] = useState("")
  const [Net_weight, setNet_weight] = useState(0)
  const [rate, setRate] = useState("")
  const [Net_Amount, setNet_Amount] = useState("")
  const [Balance, setBalance] = useState("")


  const [advance, setAdvance] = useState("")
  const [Total, setTotal] = useState(0)
  const [order_id, setorder_id] = useState(0)

  const [customer, setcustomer] = useState(0)
  const [customer_id, setcustomer_id] = useState(0)

  const [received_payment, setreceived_payment] = useState(0)
  const [Net_received, setNet_received] = useState(0)

  const [driver, setdriver] = useState("")
  const [editsuccess, seteditsuccess] = useState(false)
  const [editerr, setediterr] = useState(false)
  const [deleteerr, setdeleteerr] = useState(false)
  const [deletesuccess, setdeletesuccess] = useState(false)
  useEffect(() => {
    axios.get(`http://localhost:5000/pendingorders/${id}`)
      .then(function (response) {
        // handle success'
        setdata(response.data)
        setrow(response.data)
        console.log(response.data);
      })
      .catch(function (error) {
        // handle error
        setErr(true)
        console.log(error);
      })
  }, [Refresh, id]);

  const [flock, setflock] = useState()

useEffect(() => {
    axios.get(`http://localhost:5000/flock/${id}`)
        .then(function (response) {
            // handle success'
            console.log(response.data.flock)
            setflock(response.data.flock.flock)

        })
        .catch(function (error) {
            // handle error
            console.log(error);
        })
}, [id]);
  const handleEdit = (id) => {
    const filteredit = data.filter(data => data.id == id)
    setEditData(filteredit[0])
    setcar(filteredit[0].Car_no)
    setbroker(filteredit[0].broker)
    setF_weight(filteredit[0].f_weight)
    setdriver(filteredit[0].driver)
    setRate(filteredit[0].Rate)
    setAdvance(filteredit[0].advance)
    setorder_id(filteredit[0].id)
    setcustomer(filteredit[0].Customer.Name)
    setcustomer_id(filteredit[0].Customer.id)


  };




  const handleEditFrom = (e) => {
    e.preventDefault()
    
    axios.put(`http://localhost:5000/confirmorder/${editData.id}`, {
      customer_id: customer,
      car: car,
      broker: broker,
      f_weight: F_weight,
      driver: driver, car,
      l_weight: S_weight,
      Net_weight: Net_weight,
      Rate: rate,
      advance: advance,
      total: Total,
      received_payment: received_payment,
      Net_received: Net_received,
      Net_Amount: Net_Amount,
      Balance: Balance,
      Net_Balance: Balance < 0 ? -Net_Amount : Net_Amount,
      date: moment().format("MMM Do YY"),
      time: moment().format(" h:mm:ss a")

    })
      .then(function (response) {
        console.log("rconfirm order:  "+response.data);
        
        if (response.data.edit) {
          setcar('')
            setbroker('')
            setF_weight('')
            setdriver('')
            setTotal('')
            setRate('')
            setS_weight('')
            setNet_weight('')
            setAdvance('')
            setreceived_payment('')
            setBalance('')
            setNet_Amount('')
            seteditsuccess(true)
        } else {
          setediterr(true)
        }

        RefreshData()

      })
      .catch(function (error) {
        console.log(error);
        setediterr(true)
      });
     

  }
  const closeModel = () => {
    seteditsuccess(false)
    setediterr(false)
  }

  const handleweight = (e) => {
    setS_weight(e.target.value)
    setNet_weight(e.target.value - F_weight)
    setTotal((e.target.value - F_weight) * rate)
  }
  const handlerate = (e) => {
    setRate(e.target.value)
    setTotal(Net_weight * e.target.value)
  }

  const RefreshData = () => {
    setRefresh(Refresh + 1)
    console.log("data refresh: " + Refresh)
  };


  const deleteOrder = (id) => {
    console.log("delete id: " + id)
    axios.delete(`http://localhost:5000/order/${id}`)
      .then(function (response) {
        // handle success'
        if (response.data.delete) {
          setRefresh(Refresh + 1)
          setdeleteerr(false)
          setdeletesuccess(true)
          setTimeout(() => setdeletesuccess(false), 5000);

        } else {
          setdeleteerr(true)
        }
        console.log(response.data.msg);
        setRefresh(Refresh + 1)

      })
      .catch(function (error) {
        // handle error
        setdeleteerr(true)
        console.log(error);
      })
  };
  const printDocument = (name) => {
    const input = document.getElementById('printpendingorderdetail');
    html2canvas(input)
      .then((canvas) => {
        const imgData = canvas.toDataURL('image/jpg');

        const pdf = new jsPDF({
          orientation: "portrait",
          unit: "in",
          format: [5, 8]
        });
        pdf.addImage(imgData, 'JPEG', 0, 0);
        pdf.save(name);
        //     console.log(imgData)
        //     var myWindow = window.open("", "Image");
        // myWindow.document.write("<img src='"+imgData+"''>");
        // myWindow.print();
      })
      ;
  }
  useEffect(() => {
    searchResult()
  }, [search]);

  const searchResult = async (e) => {
    if (search) {

      const results = data.filter(data =>
        data.Customer.Name.toLowerCase().includes(search) || data.id == search || data.driver.toLowerCase().includes(search) || data.broker.toLowerCase().includes(search) || data.Car_no.toLowerCase().includes(search)
      );
      console.log("result :", results)
      setrow(results);
    } else {
      setrow(data);
      console.log("row :", row)

    }

  }
  const handlesearch = event => {
    if (event.target.value) {
      setsearch(event.target.value);

    } else {
      setrow(data);
    }
  };
  useEffect(() => {
    var received = Number(received_payment) + advance
    setNet_Amount(Total-received)
    setBalance(Total-received)
   
  }, [received_payment, advance, Net_weight, rate]);
  useEffect(() => {
    var received = Number(received_payment) + advance

    if(Balance < 0){
      let net_pay = (Number(received_payment) + advance) - (Math.abs(Balance) - Number(Net_Amount))
      setNet_received(net_pay)
      console.log("received: " + Net_received)
    }else{
      let net_pay = (Number(received_payment) + advance)
      setNet_received(net_pay)

      console.log(net_pay)
    }
  }, [received_payment, advance, Net_weight, rate, Net_Amount]);

  const simg = () => {
    html2canvas(document.getElementById('printpendingorderdetail')).then(function (canvas) {
      document.getElementById('img').appendChild(canvas);
    });
  }
  return (<>

    <div className="card" >
      <div className="card-header bg-primary ">
        <div className="row">
          <div className="col-md-5">
            <p className=" m-0 text-light pt-2">Pending Orders of Flock {flock} </p>
          </div>

          <div className="col-md-7">
            <div className="searchInput">
              <span className="icon"><IoMdSearch /></span>
              <input type="search" id="search" onChange={handlesearch} placeholder="Search..." />
            </div>
            <CreateOrder Refresh={RefreshData} flock={props.flock} />
          </div>
        </div>

      </div>
      <div className="card-body ">
        {deletesuccess && <div className="alert alert-success d-flex align-items-center" role="alert">
          <FaCheckCircle />
          <div>
            Order has been deleted successfully
          </div>
        </div>}
        {deleteerr && <div className="alert alert-danger d-flex align-items-center" role="alert">
          <MdOutlineError />
          <div>
            Order could not delete. Please try again or Restart your pc || app
          </div>
        </div>}
        {!err ? data && row ? !data.length == 0 ? <><div className="table-responsive">
          <table className="table table-bordered table-striped table-hover" >
            <caption>Pending Orders</caption>
            <thead className="">
              <tr>
                <th >#</th>
                <th >Customer</th>
                <th >Car</th>
                <th >Broker</th>
                <th >Frist Weight</th>
                <th >Price</th>
                <th >Date</th>
                <th >Actions</th>


              </tr>
            </thead>
            <tbody>
              {row.map((data, index) =>
                <tr key={index} ><td scope="row">{index + 1}</td>
                  <td >
                    <Link to={`/customer/${data.Customer.id}/${id}`}>
                      {data.Customer.Name}
                      </Link> <br />
                    <small>{data.Customer.phone}</small>
                  </td>
                  <td>{data.driver}<br />
                    <small>{data.Car_no}</small>
                  </td>
                  <td>{data.broker}
                  </td>
                  <td>{data.f_weight}
                  </td>
                  <td>{data.Rate}<br />
                    <small>{data.advance}</small>

                  </td>
                  <td>{data.Date}<br />
                    <small>{data.time}</small>
                  </td>
                  <td >

                    <button className="btn-icon bg-danger  float-end shadow action mt-2  " data-toggle="popover" aria-label="delete order"
                      onClick={() => deleteOrder(data.id)}
                      data-cooltipz-dir="bottom"><MdOutlineRemoveShoppingCart /></button>

                    <button type="button"
                      className="btn-icon bg-info float-end shadow action mt-2 " data-toggle="popover"
                      data-bs-toggle="modal"
                      data-bs-target="#confirmorder"
                      onClick={() => handleEdit(data.id)}
                      aria-label="Confirm this Order"
                      data-cooltipz-dir="bottom"><MdCheck /></button>
                    <button type="button"
                      className="btn-icon bg-warning float-end shadow action mt-2 " data-toggle="popover"
                      data-bs-toggle="modal"
                      data-bs-target="#pendingorderdetail"
                      onClick={() => handleEdit(data.id)}
                      aria-label="Order detail"
                      data-cooltipz-dir="bottom"><BiDetail /></button>
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
        </> : <>
          <img src={zero} width="600px" height="400px" />
          <h2 className="text-info">Sorry There are no pending Orders </h2>
        </> : <>
          <img src={loadingimg} width="600px" height="400px" />
          <h2 className="text-info">Loading... </h2>
        </>
          : <>
            <img src={errimg} width="600px" height="400px" />
            <h3 className="text-danger">Someting is wrong please Restart your app </h3>
          </>}
      </div>






      <div className="modal fade" id="confirmorder" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">Confirm Order</h5>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div className="modal-body">
              {!editerr ? !editsuccess ? <>
                {data ? <form onSubmit={(e) => handleEditFrom(e)}>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Customer</label>
                    <input type="Text"
                      className="form-control shadow-none"
                      disabled
                      value={customer}
                    />
                  </div>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Car #</label>
                    <input type="Text"
                      className="form-control shadow-none"
                      placeholder="Enter Car #"
                      value={car}
                      onChange={(e) => setcar(e.target.value)}
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Broker</label>
                    <input type="Text"
                      className="form-control shadow-none"
                      placeholder="Enter broker Name"
                      value={broker}
                      onChange={(e) => setbroker(e.target.value)}
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Car Driver</label>
                    <input type="Text"
                      className="form-control shadow-none"
                      placeholder="Enter Car Driver Name"
                      value={driver}
                      onChange={(e) => setdriver(e.target.value)}
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Car weight (first weight)</label>
                    <input type="number" step="0.01"
                      className="form-control shadow-none"
                      placeholder="Enter Car first weight"
                      value={F_weight}
                      onChange={(e) => setF_weight(e.target.value)}
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Car weight (second weight)</label>
                    <input type="number" step="0.01"
                      className="form-control shadow-none"
                      placeholder="Enter Car second weight"
                      value={S_weight}
                      onChange={(e) => handleweight(e)}
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Net Weight</label>
                    <input type="number" step="0.01"
                      className="form-control shadow-none"
                      placeholder="Enter Car second weight"
                      value={Net_weight}
                      disabled
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Rate</label>
                    <input type="number"
                      className="form-control shadow-none"
                      placeholder="Enter Rate"
                      value={rate}
                      onChange={(e) => handlerate(e)}
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Advance</label>
                    <input type="number"
                      className="form-control shadow-none"
                      placeholder="Enter Advance payment"
                      value={advance}
                      onChange={(e) => setAdvance(e.target.value)}
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Received Payment</label>
                    <input type="number"
                    className={`form-control shadow-none`}
                    placeholder="Enter Received payment"
                      value={received_payment}
                      onChange={(e)=> setreceived_payment(e.target.value)}
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Balance</label>
                    <input type="number" 
                    className={`form-control shadow-none `}
                    placeholder={Balance}
                    disabled
                      required
                    />
                  </div>
                  <label for="exampleInputEmail1" className="form-label">Net Amount</label>

                  {Balance <= 0 ?<div className="input-group mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="basic-addon1">-</span>
                    </div>
                    <input type="number" 
                    className={`form-control shadow-none `}
                    placeholder={Net_Amount}
                      value={Math.abs(Net_Amount)}
                      onChange={(e) => setNet_Amount(e.target.value)}
                      required
                    />
                  </div>
                  :
                  <div className="input-group mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="basic-addon1">+</span>
                    </div>
                    <input type="number" 
                    className={`form-control shadow-none `}
                    placeholder={Net_Amount}
                      value={Net_Amount}
                      onChange={(e) => setNet_Amount(e.target.value)}
                      required
                      disabled
                    />
                  </div>}
                  <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Total</label>
                    <input type="number" step="0.01"
                      className="form-control shadow-none"
                      value={Total}
                      disabled
                      required
                    />
                  </div>

                  <div className="modal-footer">
                    <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                    <button type="submit" className="btn btn-primary">Save</button>
                  </div>
                </form> : <h1>Loading..</h1>}
              </> : <>
                <img src={successimg} width="400px" height="400px" />
                <h2 className="text-success">Customer added Succesfully</h2>
                <div className="modal-footer">
                  <button type="button" className="btn btn-secondary" onClick={closeModel} data-bs-dismiss="modal">Close</button>
                </div>
              </>
                :
                <>
                  <img src={errimg} width="400px" height="400px" />
                  <h2 className="text-danger">Sorry something is wrong, Please Try Again</h2>
                  <div className="modal-footer">
                    <button type="button" className="btn btn-secondary" onClick={closeModel} data-bs-dismiss="modal">Close</button>
                  </div>
                </>
              }
            </div>
          </div>
        </div>
        {/* {editData && <EditCustomer Refresh={RefreshData}  data={editData} />} */}

      </div>

      <div className="modal fade" id="pendingorderdetail" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">Order Details</h5>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div className="modal-body" ref={componentRef}  >
              < >

                <div className="row" id="printpendingorderdetail">
                  <div className="col-md-12  pb-4 text-center">
                    <h2 className=" text-primary">Sanan Poultry Farm </h2>
                    <p><span className="bg-primary text-light p-1">Zarobai, Topi, Swabi, Khyber Pakhtunkhwa</span><br />
                      Phone: 0345-8585889</p>


                  </div>
                  <div className="row-one" >

                    <p className="p-title" >Order#</p>
                    <p className="p-value " style={{ width: "10em" }} >
                      {/* {editData && <>{editData.id <= 9 ? "000" + editData.id : editData.id <= 99 ? "00" + editData.id : editData.id <= 999 ? "0" + editData.id : editData.id}</>} */}
                      {/* {props.flock.flock} */}
                      {flock}
                      </p>
                    <p className="p-title"  >Date:</p>
                    <p className="p-value" style={{ width: "16em" }}>{editData && editData.Date} {editData && editData.time}</p>
                  </div>
                  <hr />
                  <div className="row-one" >
                    <p className="p-title" >Customer:</p>
                    <p className="p-value" >{editData && editData.Customer.Name}</p>
                    <p className="p-title" >Broker:</p>
                    <p className="p-value" >{editData && editData.broker}</p>
                  </div>
                  <hr />

                  <>
                    <div className="p-heading" >Car</div>

                    <div className="row-one" >

                      <p className="p-title" >Driver:</p>
                      <p className="p-value" >{editData && editData.driver}</p>
                      <p className="p-title" >Car#</p>
                      <p className="p-value">{editData && editData.Car_no}</p>
                    </div>
                  </>
                  <hr />

                  <div className="p-heading">Weight</div>
                  <>
                    <div className="row-one">
                      <p className="p-title" style={{ width: "15em" }}>Car weight (first weight)</p>
                      <p className="p-value">{editData && editData.f_weight}</p>
                    </div>
                  </>
                  <hr />

                  <div className="p-heading">Payments</div>
                  <>
                    <div className="row-one">
                      <div className="p-title">Rate</div>
                      <div className="p-value">{editData && editData.Rate}</div>
                      <div className="p-title">Advance</div>
                      <div className="p-value">{editData && editData.advance}</div>

                    </div>
                  </>
                  <hr />

                  {/* <div className="sign">____________________</div>
                <div className="col-md-6 pt-4">____________________</div> */}
                  <div className="row-one" style={{ justifyContent: "space-between" }}>
                    <div className="sign"  >Farm</div>
                    <div className="sign">Receiver</div>
                  </div>
                </div>

              </>
              <div style={{ display: "none" }}>
                <div ref={componentRef} style={{ width: "105mm", height: "148mm" }} id="img"></div></div>
            </div>

            <div className="modal-footer">

              <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
              <ReactToPrint
                trigger={() => <button type="button" className="btn btn-warning" data-bs-dismiss="modal">Print</button>
                }
                content={() => componentRef.current}
              />
              <button type="button" onClick={() => printDocument(editData && editData.id <= 9 ? "000" + editData.id : editData.id <= 99 ? "00" + editData.id : editData.id <= 999 ? "0" + editData.id : editData.id + " " + editData.date)} className="btn btn-primary">Download</button>
            </div>
          </div>
        </div>
      </div>


      {/* 
      <div className="modal fade" id="detail" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">Edit Order</h5>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div className="modal-body">
              {editData && <Edit data={editData} />}
            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
              <button type="button" className="btn btn-primary">Save</button>
            </div>
          </div>
        </div>
      </div> */}


    </div>
  </>
  );
}

export default PendingOrders

const Edit = (props) => {
  return (<>
    <p>{props.data[0].name}</p>
  </>
  )
}