import React, { useState, useEffect } from 'react';
import Customers from '../components/customers'
import Orders from '../components/orders'
import PendingOrders from '../components/pendingOrders'
import Sparkboxes from '../components/sparkbox'
import { Home } from './Home'
import axios from 'axios';

import {
    useParams,
    Link ,
     Outlet
   } from 'react-router-dom';
export const Flock = (props) => {
    const {id} = useParams()
console.log("flock id is: " + id)
const [flock, setflock] = useState()

useEffect(() => {
    axios.get(`http://localhost:5000/flock/${id}`)
        .then(function (response) {
            // handle success'
            console.log(response.flock.flock.flock)
            setflock(response.flock.flock)

        })
        .catch(function (error) {
            // handle error
            console.log(error);
        })
}, [id]);
console.log(flock)
    return (
        <>
        <div className="container-fluid">
        <Sparkboxes />
                <div className="row mb-5 pt-4">
                    <div className="col-md-6"><Customers  /></div>
                    <div className="col-md-6"><PendingOrders  flock={flock} /></div>
                </div>
                </div>
                <Orders flock={flock} /></>
    )
}
