import axios from 'axios';
import { FaUserEdit, FaCheckCircle } from 'react-icons/fa'
import { AiOutlineUserDelete } from 'react-icons/ai'

import loadingimg from '../images/loading.jpg'
import errimg from '../images/err.jpg'
import successimg from '../images/success.jpg'
import Customers from '../components/customers'
import React, { useState, useEffect } from 'react';
import {IoMdArrowRoundBack} from 'react-icons/io'
import {
    Link,
    useParams,
    Navigate
} from "react-router-dom";
import CustomerOrders from '../components/customerorders';
import CustomerPayments from '../components/payments';
export const Customer = () => {
    let { cid } = useParams();
    let { flock } = useParams();

    const [data , setData] = useState()
    const [customer , setcustomer] = useState()
    const [orders , setorders] = useState()
    const [total , setTotal] = useState(0)
    const [successpay , setsuccesspay] = useState(0)
    const [Balance , setBalance] = useState(0)



    const [Refresh, setRefresh] = useState(0)

    

    useEffect(() => {
      axios.get(`http://localhost:5000/customer/${cid}`)
        .then(function (response) {
          // handle success'
          setcustomer(response.data.customer)
          setorders(response.data.orders)
          setTotal(response.data.Total_payment)
          setsuccesspay(response.data.received_payment)
          setBalance(response.data.pandingpayment)
          console.log(response.data.response.data);
        })
        .catch(function (error) {
          // handle error
          console.log(error);
        })
    }, [Refresh]);
    console.log("total: " + total)
    console.log("successpay: " + successpay)
    const RefreshData = () => {
        setRefresh(Refresh + 1)
        console.log("data refresh: " + Refresh)
      };
      const deleteCustomer = () => {
        console.log("delete id: " + cid)
        axios.delete(`http://localhost:5000/customer/${cid}`)
          .then(function (response) {
            // handle success'
            if (response.data.delete) {
                setdeletes(true)
            } else {
            }
    
          })
          .catch(function (error) {
            console.log(error);
          })
      };
      //edit form
  const [name, setName] = useState("")
  const [phone, setPhone] = useState("")
  const [phone1, setPhone1] = useState("")
  const [address, setAddress] = useState("")
  const [des, setDes] = useState("")
  const [editsuccess, seteditsuccess] = useState(false)
  const [editerr, setediterr] = useState(false)
  const [deletes, setdeletes] = useState(false)

  const handleEdit = () => {
    setName(customer.Name)
    setPhone(customer.phone)
    setPhone1(customer.phone1)
    setAddress(customer.address)
    setDes(customer.des)
  };
  const handleEditFrom = () => {
    

    axios.put(`http://localhost:5000/customer/${cid}`, {
    Name: name,
    phone: phone,
    phone1: phone1,
    address: address,
    des: des}
     )
      .then(function (response) {
        console.log(response);
        if (response.data.update) {
          setName('')
          setPhone('')
          setPhone1('')
          setAddress('')
          setDes('')
          seteditsuccess(true)
        } else {
          setediterr(true)
        }

        RefreshData()

      })
      .catch(function (error) {
        console.log(error);
        setediterr(true)
      });
  }
  const closeModel = () => {
    seteditsuccess(false)
    setediterr(false)
  }

    return (deletes ? <><Navigate to={`/home/${flock}`} /></>:  <>
    {/*Totle Data Blocks */}
    <div className="container-fluid">
        {/* <C_Sparkboxes /> */}
        <div class="alert alert-secondary mt-4" role="alert">
            <Link to={`/home/${flock}`}>
            <IoMdArrowRoundBack/> Back To Home
            </Link>
            </div>
        <div className="row pt-2">
            <div className="col-md-2">
                <ul class="list-group">
                    <li class="list-group-item active" aria-current="true">
                        <p className="list-title">Customer:</p>
                        <p className="list-value">{customer && customer.Name}</p>
                    </li>
                    <li class="list-group-item">
                        <p className="list-title">Contacts:</p>
                        <p className="list-value">{customer && customer.phone}</p>
                        <p className="list-value">{customer && customer.phone1}</p>
                    </li>
                    <li class="list-group-item">
                        <p className="list-title">Address:</p>
                        <p className="list-value">{customer && customer.address}</p>
                    </li>
                    <li class="list-group-item">
                        <p className="list-title">Orders:</p>
                        <p className="list-value">{orders && orders.length? orders.length: '0'}</p>
                    </li>
                    <li class="list-group-item">
                        <p className="list-title">Balance:</p>
                        <p className="list-value">Rs. { Balance}</p>
                    </li>
                    <li class="list-group-item">
                        <p className="list-title">Received Payments:</p>
                        <p className="list-value">Rs. {successpay}</p>
                    </li>
                    <li class="list-group-item">
                        <p className="list-title">Date:</p>
                        <p className="list-value">{customer && customer.date}</p>
                    </li>
                    <li class="list-group-item">
                        <p className="list-title">description:</p>
                        <p className="list-value">{customer && customer.des}</p>
                    </li>
                    <li class="list-group-item">
                        <p className="list-title">Actions:</p>
                        <button className="btn-icon bg-danger action bg-info mt-2 float-end shadow" data-toggle="popover" onClick={() => deleteCustomer()} aria-label="Delete Customer" data-cooltipz-dir="bottom"><AiOutlineUserDelete /></button>

            <button className="btn-icon bg-warning action bg-info mt-2 float-end shadow" data-toggle="popover" aria-label="Edit Customer" data-cooltipz-dir="bottom"
              data-bs-toggle="modal"
              data-bs-target="#edit"
              onClick={() => handleEdit()}
            ><FaUserEdit /></button>
                    </li>
                </ul>
            </div>
            <div className="col-md-10">
                <CustomerOrders data={orders} RefreshData={RefreshData} customer={customer && customer}/>
                


            </div>
        </div>
        <div className="modal fade" id="edit" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
<div className="modal-dialog">
  <div className="modal-content">
    <div className="modal-header">
      <h5 className="modal-title" id="exampleModalLabel">Edit Customer</h5>
      <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
    </div>
    <div className="modal-body">
      {!editerr ? !editsuccess ? <>
        {customer ? <>
          <div className="mb-3">
            <label for="exampleInputEmail1" className="form-label">Name</label>
            <input type="Text"
              className="form-control shadow-none"
              placeholder="Enter Customer Name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              required
            />
          </div>
          <div className="row">
            <div className="col-md-6 mb-3">
              <label for="exampleInputEmail1" className="form-label">Phone 1</label>
              <input type="Text"
                className="form-control shadow-none"
                placeholder="Enter phone number"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                required
              />
            </div>
            <div className="col-md-6 mb-3">
              <label for="exampleInputEmail1" className="form-label">Phone 2</label>
              <input type="Text"
                placeholder="Enter another phone number"
                className="form-control shadow-none"
                value={phone1}
                onChange={(e) => setPhone1(e.target.value)}
              />
            </div>
          </div>
          <div className=" mb-3">
            <label for="exampleInputEmail1" className="form-label">Address</label>
            <input type="Text"
              placeholder="Enter Customer Address"
              className="form-control shadow-none"
              value={address}
              onChange={(e) => setAddress(e.target.value)}
            />
          </div>
          <div className="mb-3">
            <label for="exampleInputEmail1" className="form-label">Description</label>
            <textarea type="Text"
              className="form-control shadow-none"
              placeholder="Enter Customer Description "
              value={des}
              onChange={(e) => setDes(e.target.value)}
            />
          </div>
          <div className="modal-footer">
            <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
            <button type="submit" onClick={handleEditFrom} className="btn btn-primary">Save</button>
          </div>
        </> : <h1>Loading..</h1>}
      </> : <>
        <img src={successimg} width="400px" height="400px" />
        <h2 className="text-success">Customer added Succesfully</h2>
        <div className="modal-footer">
          <button type="button" className="btn btn-secondary" onClick={closeModel} data-bs-dismiss="modal">Close</button>
        </div>
      </>
        :
        <>
          <img src={errimg} width="400px" height="400px" />
          <h2 className="text-danger">Sorry something is wrong, Please Try Again</h2>
          <div className="modal-footer">
            <button type="button" className="btn btn-secondary" onClick={closeModel} data-bs-dismiss="modal">Close</button>
          </div>
        </>
      }
    </div>
  </div>
</div>
{/* {editData && <EditCustomer Refresh={RefreshData}  data={editData} />} */}

</div>
    </div>

</>
       
    )
}
