import React, { useState, useEffect } from 'react';
import { FaUserPlus } from 'react-icons/fa'
import { MdOutlineAddShoppingCart, MdRefresh } from 'react-icons/md'
import moment from 'moment';
import axios from 'axios'
import successimg from '../../images/success.jpg'
import errimg from '../../images/err.jpg'
import {  useParams, } from 'react-router-dom';

function CreateOrder(props) {
    const {id} = useParams()
    const [car, setcar] = useState("")
    const [customer_id, setcustomer_id] = useState("")
    const [broker, setbroker] = useState("")
    const [customers, setcustomers] = useState([])
    const [weight, setweight] = useState("")
    const [driver, setdriver] = useState("")
    const [success, setsuccess] = useState(false)
    const [err, seterror] = useState(false)
    const [rate, setRate] = useState("")
    const [advance, setAdvance] = useState("")
    const [Refresh, setRefresh] = useState(1)
    const [flock_id, setflock_id] = useState(props.flock)


    useEffect(() => {
        setsuccess(false)
        seterror(false)
        setflock_id(props.flock)
    }, [props]);
    useEffect(() => {
        axios.get('http://localhost:5000/customers')
            .then(function (response) {
                // handle success'
                setcustomers(response.data.customers)
            })
            .catch(function (error) {
                // handle error
                seterror(true)
                console.log(error);
            })
    }, [Refresh]);

    const handleSubmit = (e) => {
        e.preventDefault()

        axios.post('http://localhost:5000/order', {
            car: car,
            Customer_id: customer_id,
            broker: broker,
            weight: weight,
            driver: driver,
            Rate: rate,
            flock_id: id,
            advance: advance,
            date: moment().format("MMM Do YY"),
            time: moment().format(" h:mm:ss a")

        })
            .then(function (response) {
                console.log(response);
                setcar('')
                setcustomer_id('')
                setbroker('')
                setweight('')
                setdriver('')
                setRate('')
                setAdvance('')
                seterror(false)
                setsuccess(true)
                props.Refresh()

            })
            .catch(function (error) {
                console.log(error);
                seterror(true)
            });
    }
    const closeModel = () => {
        setsuccess(false)
        seterror(false)

    }
    console.log(customer_id)
    return (<>
        <button className="btn-icon bg-info  float-end shadow" data-bs-toggle="modal" data-bs-target="#ceateorders" data-toggle="popover" aria-label="Create Order" data-cooltipz-dir="bottom"><MdOutlineAddShoppingCart /></button>
        <div className="modal fade" id="ceateorders" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title" id="exampleModalLabel">Create Order</h5>
                        <button type="button" className="btn-close" onClick={closeModel} data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div className="modal-body">
                        {!err ? !success ? <>
                            <form onSubmit={(e) => handleSubmit(e)}>

                                <div className="mb-3">
                                    <label for="exampleInputEmail1" className="form-label">Customer <span data-toggle="popover" aria-label="Refresh"
              onClick={() => setRefresh(Refresh + 1)}
              data-cooltipz-dir="bottom"><MdRefresh /></span></label>
                                    <select className="form-select shadow-none" aria-label="Default select example"
                                        onChange={(e) => setcustomer_id(e.target.value)}
                                    >
                                        <option selected>Select Customer</option>
                                        {customers.map((data) =>
                                            <option value={data.id}>{data.Name} ({data.address})</option>

                                        )}
                                    </select>
                                </div>
                                <div className="mb-3">
                                    <label for="exampleInputEmail1" className="form-label">Car #</label>
                                    <input type="Text"
                                        className="form-control shadow-none"
                                        placeholder="Enter Car #"
                                        value={car}
                                        onChange={(e) => setcar(e.target.value)}
                                        required
                                    />
                                </div>
                                <div className="mb-3">
                                    <label for="exampleInputEmail1" className="form-label">Broker</label>
                                    <input type="Text"
                                        className="form-control shadow-none"
                                        placeholder="Enter broker Name"
                                        value={broker}
                                        onChange={(e) => setbroker(e.target.value)}
                                        required
                                    />
                                </div>
                                <div className="mb-3">
                                    <label for="exampleInputEmail1" className="form-label">Car Driver</label>
                                    <input type="Text"
                                        className="form-control shadow-none"
                                        placeholder="Enter Car Driver Name"
                                        value={driver}
                                        onChange={(e) => setdriver(e.target.value)}
                                        required
                                    />
                                </div>
                                <div className="mb-3">
                                    <label for="exampleInputEmail1" className="form-label">Rate</label>
                                    <input type="number"
                                        className="form-control shadow-none"
                                        placeholder="Enter Rate"
                                        value={rate}
                                        onChange={(e) => setRate(e.target.value)}
                                        required
                                    />
                                </div>
                                <div className="mb-3">
                                    <label for="exampleInputEmail1" className="form-label">Advance</label>
                                    <input type="number"
                                        className="form-control shadow-none"
                                        placeholder="Enter Advance payment"
                                        value={advance}
                                        onChange={(e) => setAdvance(e.target.value)}
                                        required
                                    />
                                </div>
                                <div className="mb-3">
                                    <label for="exampleInputEmail1" className="form-label">Car weight (first weight)</label>
                                    <input type='number' step="0.01"
                                        className="form-control shadow-none"
                                        placeholder="Enter Car first weight"
                                        value={weight}
                                        onChange={(e) => setweight(e.target.value)}
                                        required
                                    />
                                </div>



                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                    <button type="submit" className="btn btn-primary">Save</button>
                                </div>
                            </form>
                        </> : <>
                            <img src={successimg} width="400px" height="400px" />
                            <h2 className="text-success">Customer added Succesfully</h2>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" onClick={closeModel} data-bs-dismiss="modal">Close</button>
                            </div>
                        </>
                            :
                            <>
                                <img src={errimg} width="400px" height="400px" />
                                <h2 className="text-danger">Sorry something is wrong, Please Try Again</h2>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" onClick={closeModel} data-bs-dismiss="modal">Close</button>
                                </div>
                            </>
                        }

                    </div>

                </div>
            </div>
        </div>
    </>
    );
}

export default CreateOrder
