import React, { useState, useEffect } from 'react';

import axios from 'axios';
import { MdRefresh, MdAddCircleOutline } from 'react-icons/md'
import { AiOutlineClose } from 'react-icons/ai'
import moment from 'moment';
import wait from '../images/waiting.jpg'
import {
   useParams,
   Link ,
    Outlet
  } from 'react-router-dom';
export const Plack = (props) => {
const {id} = useParams()
    const [data, setData] = useState([])
    const [Eflock, setflock] = useState()
    const [Iserr, setErr] = useState(false)
    const [IsDropdown, setDropdown] = useState(false)

    const [C_flock, setC_flock] = useState()
    const [DeleteId, setDeleteId] = useState()

    const [deleteerr, setdeleteerr] = useState(false)
    const [deletesuccess, setdeletesuccess] = useState(false)
    const [Refresh, setRefresh] = useState(0)
    const [IsEntry, setIsEntry] = useState(false)
    const [success, setsuccess] = useState(false)
    const [err, seterror] = useState(false)
    const [Isflock, setIsflock] = useState(false)

    useEffect(() => {
        axios.get('http://localhost:5000/flock')
            .then(function (response) {
                // handle success'
                console.log(response.data)
                setData(response.data.flocks)
                props.flock(response.data.flocks)

            })
            .catch(function (error) {
                // handle error
                setErr(true)
                console.log(error);
            })
    }, [Refresh]);

    useEffect(() => {
        setsuccess(false)
        seterror(false)
    }, []);

    const handleSubmit = (e) => {
        if(Eflock){
        axios.post('http://localhost:5000/api/flock', {
            headers: {
                'Content-Type': 'application/json'
            },
            flock: Eflock,
            date: moment().format("MMM Do YY") + " " + moment().format(" h:mm:ss a")
        })
            .then(function (response) {
                console.log(response);
                setflock('')
                setsuccess(true)
                setRefresh(Refresh + 1)
                setIsEntry(!IsEntry)
            })
            .catch(function (error) {
                console.log(error);
                seterror(true)
            });
        
        }else{
            setIsEntry(false)
        }
    }

    const handledelete = (id) => {
        setDeleteId(id)
        setdeleteerr(false)
              setdeletesuccess(false)
      };
      const deleteflock = () => {
        axios.delete(`http://localhost:5000/flock/${DeleteId}`)
          .then(function (response) {
            // handle success'
            if (response.data.delete) {
              setRefresh(Refresh + 1)
              setdeleteerr(false)
              setdeletesuccess(true)
    
            } else {
              setdeleteerr(true)
            }
            console.log(response.data.msg);
            setRefresh(Refresh + 1)
    
          })
          .catch(function (error) {
            // handle error
            setdeleteerr(true)
            console.log(error);
          })
      };

      const isflock=()=>{
        setDropdown(false)
        // let flock = id && data.length>0? data.filter(data =>data.id == id)[0].flock :"Select Flock";
        // localStorage.setItem('flock', flock)
      }
    return (
        <>
            {/*Totle Data Blocks */}
            <div className="container-fluid">
                <div className="row sparkboxes">
                    <div className="col-md-12">
                        <div className="box box1 ">
                            <div className="row mb-2">
                                <div className="col-md">
                                    <h2 className="spark-box-value">Sanan Poultry Farm </h2>
                                </div>
                                <div className="col-md d-flex justify-content-end">
                                    <>
                                        {IsEntry ? <> <input type="text" className=" plack-input"
                                            placeholder="Enter New Flock"
                                            value={Eflock}
                                            onChange={(e) => setflock(e.target.value)}
                                        ></input>
                                            <button className="btn-icon   float-end shadow" style={{ marginLeft: ".5em", }} data-toggle="popover" aria-label="Add New"
                                                onClick={() => handleSubmit()}
                                                data-cooltipz-dir="bottom"><MdAddCircleOutline /></button></> 
                                                
                                                
                                                : 
                                                
                                                <> <label className="dropdown">

                                                    <div className="dd-button" onClick={()=> setDropdown(true)}>
                                                        {id && data.length>0? data.filter(data =>data.id == id)[0].flock :"Select Flock"}
                                                    </div>

                                                    <input type="checkbox" className="dd-input" id="test" />

                                                    {IsDropdown && <ul className="dd-menu">
                                                        {Iserr ? <li>error...</li> :
                                                            data.map((data) =>
                                                                <li onClick={isflock} ><>
                                                                    <Link   to={`home/${data.id}`} >{data.flock}</Link > 
                                                                     {id != data.id && <span className="text-danger  float-end " data-toggle="popover" aria-label={data.flock}
                                                                    data-bs-toggle="modal"
                                                                    data-bs-target="#deleteflock"
                                                                    onClick={()=> handledelete(data.id)}
                                                                        data-cooltipz-dir="bottom"><AiOutlineClose /></span>}<br></br>
                                                                        <small>{data.data}</small>
                                                                </>
                                                                </li>
                                                            )
                                                        }

                                                       
                                                    </ul>}

                                                </label>
                                            <button className="btn-icon   float-end shadow" style={{ marginLeft: ".5em", }} data-toggle="popover" aria-label="Add New"
                                                onClick={() => setIsEntry(!IsEntry)}
                                                data-cooltipz-dir="bottom"><MdAddCircleOutline /></button> </>}



                                    </>
                                </div>
                            </div>
                            <button className="btn-icon   float-end shadow" style={{ marginRight: ".5em" }} data-toggle="popover" aria-label="refresh states"
                                onClick={() => setRefresh(Refresh + 1)}
                                data-cooltipz-dir="bottom"><MdRefresh /></button>
                        </div>
                    </div>
                </div>

                 {!id && <div className="ps-5 float-center ">
                 <img className="col-md-8 " src={wait}  width="900px" height="600px" />
                 <h1 className="col-md-12 text-danger">Please Select Flock </h1>
               </div>}
            </div>
 
      <div className="modal fade" id="deleteflock" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            
            <div className="modal-body">
                {!deleteerr && deletesuccess ?  <p className="text-danger">The flock is deleted successfully</p>: <>
                <h3 className="text-danger">Are You sure to delete this flock</h3>
                <p className="text-danger">warning if you delete the flock you will lost all the related data with this flock</p></>
            }
               

            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
              {!deletesuccess && <button type="button" className="btn btn-danger" onClick={()=> deleteflock()}>Yes</button>}
         </div>
          </div>
        </div>
      </div>
        </>
    )
}
