import React, { useState, useEffect } from 'react';
import { FaUserPlus } from 'react-icons/fa'
import moment from 'moment';
import axios from 'axios'
import successimg from '../../images/success.jpg'
import errimg from '../../images/err.jpg'

function AddCustomer(props) {
    const [name, setName] = useState("")
    const [phone, setPhone] = useState("")
    const [phone1, setPhone1] = useState("")
    const [address, setAddress] = useState("")
    const [des, setDes] = useState("")
    const [success, setsuccess] = useState(false)
    const [err, seterror] = useState(false)
    useEffect(() => {
        setsuccess(false)
        seterror(false)
      }, []);

    const handleSubmit = (e) => {
        e.preventDefault()
        axios.post('http://localhost:5000/api/customer', {
            headers: {
                'Content-Type': 'application/json'
            },
            Name: name,
            phone: phone,
            phone1: phone1,
            address: address,
            des: des,
            date: moment().format("MMM Do YY")
        })
            .then(function (response) {
                console.log(response);
                setName('')
                setPhone('')
                setPhone1('')
                setAddress('')
                setDes('')
                setsuccess('')
                setsuccess(true)
                props.Refresh()

            })
            .catch(function (error) {
                console.log(error);
                seterror(true)
            });
    }
    const closeModel = () => {
        setsuccess(false)
        seterror(false)

    }
    return (<>
        <button className="btn-icon bg-info  float-end shadow" data-bs-toggle="modal" data-bs-target="#exampleModal" data-toggle="popover" aria-label="Add New Customer" data-cooltipz-dir="bottom"><FaUserPlus /></button>
        <div className="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title" id="exampleModalLabel">Add New Customer</h5>
                        <button type="button" className="btn-close" onClick={closeModel} data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div className="modal-body">
                        {!err ? !success ? <>
                            <form onSubmit={(e)=> handleSubmit(e)}>
                                <div className="mb-3">
                                    <label for="exampleInputEmail1" className="form-label">Name</label>
                                    <input type="Text"
                                        className="form-control shadow-none"
                                        placeholder="Enter Customer Name"
                                        value={name}
                                        onChange={(e) => setName(e.target.value)}
                                        required
                                    />
                                </div>
                                <div className="row">
                                    <div className="col-md-6 mb-3">
                                        <label for="exampleInputEmail1" className="form-label">Phone 1</label>
                                        <input type="Text"
                                            className="form-control shadow-none"
                                            placeholder="Enter phone number"
                                            value={phone}
                                            onChange={(e) => setPhone(e.target.value)}
                                            required
                                        />
                                    </div>
                                    <div className="col-md-6 mb-3">
                                        <label for="exampleInputEmail1" className="form-label">Phone 2</label>
                                        <input type="Text"
                                            placeholder="Enter another phone number"
                                            className="form-control shadow-none"
                                            value={phone1}
                                            onChange={(e) => setPhone1(e.target.value)}
                                        />
                                    </div>
                                </div>
                                <div className=" mb-3">
                                    <label for="exampleInputEmail1" className="form-label">Address</label>
                                    <input type="Text"
                                        placeholder="Enter Customer Address"
                                        className="form-control shadow-none"
                                        value={address}
                                        onChange={(e) => setAddress(e.target.value)}
                                    />
                                </div>
                                <div className="mb-3">
                                    <label for="exampleInputEmail1" className="form-label">Description</label>
                                    <textarea type="Text"
                                        className="form-control shadow-none"
                                        placeholder="Enter Customer Description "
                                        value={des}
                                        onChange={(e) => setDes(e.target.value)}
                                    />
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                    <button type="submit" onClick={handleSubmit} className="btn btn-primary">Save</button>
                                </div>
                            </form>
                        </> : <>
                            <img src={successimg} width="400px" height="400px" />
                            <h2 className="text-success">Customer added Succesfully</h2>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" onClick={closeModel} data-bs-dismiss="modal">Close</button>
                            </div>
                        </>
                            :
                            <>
                                <img src={errimg} width="400px" height="400px" />
                                <h2 className="text-danger">Sorry something is wrong, Please Try Again</h2>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                </div>
                            </>
                        }

                    </div>

                </div>
            </div>
        </div>
    </>
    );
}

export default AddCustomer
